.container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: -10px auto;
    position: relative;
    z-index: 0;
}

.textStatus {
  text-align: start;
}

.number {
    font-size: 20px;  
    margin-left: 20px; 
    position: absolute; 
    right: -5px;
}

.iconsContainer{
    position: absolute;
    left: -30px;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.answerSquare {
  flex-shrink: 0;
  -webkit-appearance:none;
  display:inline-block;
  width:10px;
  height:10px;
  padding:0px;
  margin:0px;
  margin-left:5px;
  margin-top: 6px;
  background:#F3F2DC;
  border-radius:5px;
  border:3px solid #025D66;
  border: 3px solid #025D66;                                                
  box-sizing:content-box;                                                   
  line-height:18px;                                                         
  vertical-align: sub;                                                      
  position:relative;
}

.after{ 
 background-color: #025D66;
 
}


.ldsRing {
    display: inline-block;
    position: absolute;
    width: 20px;
    height: 20px;
    left: -20px;
    top: 0;
  }
  .ldsRing div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 16px;
    height: 16px;
    margin: 2px;
    border: 2px solid #A5A5A5;
    border-radius: 50%;
    animation: ldsRing 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #A5A5A5 transparent transparent transparent;
  }
  .ldsRing div:nth-child(1) {
    animation-delay: -0.45s;
  }
  .ldsRing div:nth-child(2) {
    animation-delay: -0.3s;
  }
  .ldsRing div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes ldsRing {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  